// Firebase configuration using environment variables
import { initializeApp } from 'firebase/app';
import { getAuth, GoogleAuthProvider, EmailAuthProvider } from 'firebase/auth';
import { getStorage } from 'firebase/storage';

// Firebase configuration using environment variables from webpack
const firebaseConfig = {
  apiKey: process.env.FIREBASE_API_KEY,
  authDomain: process.env.FIREBASE_AUTH_DOMAIN,
  projectId: process.env.FIREBASE_PROJECT_ID,
  storageBucket: process.env.FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.FIREBASE_APP_ID
};

// Validate configuration
const requiredKeys = ['apiKey', 'authDomain', 'projectId', 'storageBucket', 'messagingSenderId', 'appId'];
const missingKeys = requiredKeys.filter(key => !firebaseConfig[key]);

if (missingKeys.length > 0) {
  console.error(' Missing Firebase configuration keys:', missingKeys);
  console.log(' Please create a .env file with your Firebase credentials, or update src/firebase-config.js directly');
  console.log(' See .env.example for required environment variables');
  
  // Show user-friendly error in the UI
  if (typeof window !== 'undefined') {
    setTimeout(() => {
      const errorDiv = document.createElement('div');
      errorDiv.style.cssText = `
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        background: #dc2626;
        color: white;
        padding: 16px;
        text-align: center;
        z-index: 10000;
        font-family: system-ui, sans-serif;
      `;
      errorDiv.innerHTML = `
         <strong>Firebase Configuration Missing</strong><br>
        Please create a .env file with your Firebase credentials or contact the developer.
      `;
      document.body.appendChild(errorDiv);
    }, 1000);
  }
  
  // For development, provide a warning but don't throw error
  console.warn(' Using demo Firebase configuration. App functionality will be limited.');
  
  // Use demo configuration to allow app to load
  Object.assign(firebaseConfig, {
    apiKey: "demo-api-key",
    authDomain: "demo-project.firebaseapp.com",
    projectId: "demo-project", 
    storageBucket: "demo-project.firebasestorage.app",
    messagingSenderId: "123456789",
    appId: "1:123456789:web:demo-app-id"
  });
}

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const googleProvider = new GoogleAuthProvider();
const emailProvider = new EmailAuthProvider();
const storage = getStorage(app);

export { auth, googleProvider, emailProvider, storage };
